.header {
  position: relative;

  padding-top: 40px;
  padding-bottom: 35px;

  @media (min-width: @tabletWidth) {
    padding-top: 40px;
    padding-bottom: 54px;
  }

  @media (min-width: @desktopWidth) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    .container(@desktopContainer);
    padding-bottom: 72px;
  }
}

.header__wrapper-logo {
  position: relative;

  .container(@mobileContainer);
  margin-bottom: 15px;

  @media (min-width: @tabletWidth) {
    .container(@tabletContainer);
    margin-bottom: 46px;
  }

  @media (min-width: @desktopWidth) {
    width: 35%;
    margin-left: 0;
    margin-right: 0;
    margin-bottom: 30px;
  }
}

.header__title {
  display: block;

  font-family: 'Special-Elite', 'Arial', sans-serif;
  .fontTitleMobile();
  line-height: 1;
  font-weight: 400;
  text-transform: lowercase;

  @media (min-width: @tabletWidth) {
    .fontTitleTablet();
    line-height: 1;
  }

  @media (min-width: @desktopWidth) {
    .fontTitleDesktop();
    line-height: 1;
  }
}

.header__caption {
  margin-top: 0;
  margin-bottom: 0;

  font-family: 'Roboto-Mono', 'Arial', sans-serif;
  .fontTextMobile();
  font-weight: 300;
  font-weight: var(--weightFont3, 300);
  text-transform: lowercase;

  @media (min-width: @tabletWidth) {
    .fontTextTablet();
  }

  @media (min-width: @desktopWidth) {
    .fontTextDesktop();
    line-height: 45px;
  }
}

.header__wrapper-menu {
  max-height: 0;
  overflow: hidden;

  transition: max-height 0.4s ease-out;

  @media (min-width: @tabletWidth) {
    max-height: none;

    overflow: visible;
  }

  @media (min-width: @desktopWidth) {
    height: 100%;
  }
}

.header__image {
  display: block;
  width: 100%;
  height: auto;
  margin-bottom: 15px;

  @media (min-width: @tabletWidth) {
    margin-bottom: 24px;
  }

  @media (min-width: @desktopWidth) {
    margin-bottom: 32px;
  }
}

.header__text {
  .container(@mobileContainer);
  margin-bottom: 0;

  font-family: 'Roboto-Mono', 'Arial', sans-serif;
  .fontTextMobile();
  font-weight: 300;
  font-weight: var(--weightFont3, 300);

  @media (min-width: @tabletWidth) {
    .container(@tabletContainer);

    .fontTextTablet();
  }

  @media (min-width: @desktopWidth) {
    width: 100%;
    margin-left: 0;
    margin-right: 0;

    .fontTextDesktop();
  }
}
